body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body, html {
    margin: 0;
    padding: 0;
    overflow: hidden;
}


main{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: row;
}

#left {
    color: black;
    width: 50%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

}

#right {
    color: white;
    width: 50%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-transition: .5s;
    transition: .5s;
}

#right div{
    -webkit-transition: 1s;
    transition: 1s;
}

.first {
    background-color: rgb(255, 95, 95);
}

.second {
    background-color: rgb(131, 95, 252);
}

.third {
    background-color: rgb(249, 220, 92);
}

.fourth {
    background-color: rgb(249, 157, 212);
}

nav{
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: space-around;
    position: absolute;
}
